<script> 
  import { onMount } from 'svelte';
  import { gsap } from "gsap";
  import { TextPlugin } from "gsap/TextPlugin";

  gsap.registerPlugin(TextPlugin);

  //reset tl function
  const resetTL = function() {
    this.restart().pause();
  };

  //text animation TLs
  const desTL = gsap.timeline({onComplete: resetTL, repeat: 1, repeatDelay: 1, yoyo: true, paused: true}),
        animTL = gsap.timeline({onComplete: resetTL, paused: true}),
        codeTL = gsap.timeline({onComplete: resetTL, paused: true});

  //speech bubble TL
  const speechTL = gsap.timeline({delay: 1});


  const delay = .02,
        duration = .01,
        // define nudge size for 'designer'
        nudgeAmount = 7,
        nudges = {x:0, y:0};

  onMount(() => {
    // animate speech bubble
    speechTL.from('.speech', {
      opacity: 0,
      duration: .2,
    })
    .from('.speech', {
      transformOrigin: "center bottom",
      scale: .3,
      duration: .8,
      ease: "elastic.out(1, 0.3)",
    }, "<")
    .from('.speech', {
      duration: 3,
      transformOrigin: "center bottom",
      rotate: 40,
      ease: "elastic.out(1, 0.12)",
    }, "<")
    .from('.hi', {
      opacity: 0,
      duration: .5,
      scale: .2,
      ease: "power4.out",
      transformOrigin: "50%, 50%"
    }, "-=2.6")
    .fromTo('.bang', {
      opacity: 0,
      scale: .8,
      rotate: 45,
      transformOrigin: "50%, 50%"
    }, {
      duration: .5,
      opacity: 1,
      rotate: 15,
      scale: 1.3,
      x: 10,
      y: 0,
      ease: "power4.out",
      transformOrigin: "50%, 50%"
    }, "-=2.2");

    // track nudge count by dimension
    const nudge = function(dim) {
      nudges[dim]++;
    }
    // word 'designer' nudges up and left, guides show up, color picker appear, color changed, yoyo
    const desSel = ' .designer';

    desTL.set('.guide-y-ends', {
      autoAlpha: 0
    })
    .set('.guide-x-ends', {
      autoAlpha: 0
    })
    .to(desSel, {
      top: 0
    })
    .add(nudge("y"))
    .to(desSel, {
      duration,
      top: (nudges.y * nudgeAmount * -1),
    })
    .to('.guide-y-line', {
      duration,
      height: nudges.y * nudgeAmount,
    })
    .to('.guide-y-ends', {
      autoAlpha: 1
    })
    .add(nudge("y"))
    .to(desSel, {
      delay,
      duration,
      top: (nudges.y * nudgeAmount * -1),
    })
    .to('.guide-y-line', {
      duration,
      height: nudges.y * nudgeAmount,
    })
    .add(nudge("x"))
    .to(desSel, {
      delay,
      duration,
      left: (nudges.x * nudgeAmount * -1),
    })
    .to('.guide-x-line', {
      duration,
      width: nudges.x * nudgeAmount,
    }).to('.guide-x-ends', {
      autoAlpha: 1
    })
    .add(nudge("x"))
    .to(desSel, {
      delay,
      duration,
      left: (nudges.x * nudgeAmount * -1),
    })
    .to('.guide-x-line', {
      duration,
      width: nudges.x * nudgeAmount,
    })
    .add(nudge("x"))
    .to(desSel, {
      delay: .5,
      duration,
      left: (nudges.x * nudgeAmount * -1),
    })
    .to('.guide-x-line', {
      duration,
      width: nudges.x * nudgeAmount,
    })
    .add(nudge("y"))
    .to(desSel, {
      delay: .5,
      duration,
      top: (nudges.y * nudgeAmount * -1),
    })
    .to('.guide-y-line', {
      duration,
      height: nudges.y * nudgeAmount,
    }).timeScale(2.1);

    animTL.to('.animator', {
      delay: .3,
      duration: .2,
      scaleY: 1.2,
      ease: "ease-out",
    })
    .to('.animator', {
      delay: .1,
      duration: 1,
      scaleY: .5,
      ease: "elastic.out(1, 0.3)",
    })
    .to('.animator', {
      delay: .1,
      duration: .2,
      scaleX: 1.2,
      ease: "ease-out",
    })
    .to('.animator', {
      delay: .1,
      duration: 1,
      scaleX: .5,
      ease: "elastic.out(1, 0.3)",
    })
    .to('.animator', {
      delay: .2,
      duration: 1,
      scale: 1,
      rotate: 360,
      ease: "elastic.out(1, 0.3)",
    }).timeScale(1.7);

    codeTL.set('.code-tag', {
      margin: '.4rem',
      autoAlpha: 1,
    })
    .to('.code-open', {
      duration: .4,
      text: "&lt;i",
      ease: "none"
    })
    .to('.code-close', {
      duration: .5,
      text: "&lt;/i",
      ease: "none"
    }, "<")
    .to ('.code-open', {
      delay: .4,
      duration: .1,
      text: "&lt;",
      ease: "none"
    })
    .to('.code-close', {
      duration: .1,
      text: "&lt;/",
      ease: "none"
    }, "<")
    .to ('.code-open', {
      duration: .4,
      text: "&lt;em&gt;",
      ease: "none"
    })
    .to('.code-close', {
      duration: .4,
      text: "&lt;/em&gt;",
      ease: "none"
    }, "<")
    .to('.code-em', {
      delay: .1,
      duration: .1,
      skewX: -20,
      ease: "ease-out"
    })
    .to ('.code-open', {
      delay: 1,
      duration: .4,
      text: "",
      ease: "none"
    })
    .to('.code-close', {
      duration: .4,
      text: "",
      ease: "none"
    }, "<")
    .to('.code-em', {
      duration: .2,
      skewX: 0,
      ease: "ease-out"
    })
    .set('.code-tag', {
      margin: '0',
      autoAlpha: 0,
    }).timeScale(1.5);
  });
</script>

<section>
  <div class="speech">
    <svg 
      class="bubble" 
      width="100%" 
      viewBox="0 0 312 351" 
      fill="none" 
      xmlns="http://www.w3.org/2000/svg"
    >
      <path 
        vector-effect="non-scaling-stroke"
        class="speech-outer"
        d="M107.5 282L30.4 274.7C13.4 273.1 0.5 259 0.5 241.8V33.5C0.5 15.3 15.3 0.5 33.4 0.5C35.2 0.5 37 0.6 38.8 0.9L283.8 41.2C299.8 43.8 311.4 57.5 311.4 73.8V265.1C311.4 274 307.9 282.3 301.7 288.5C295.5 294.7 287.2 298.1 278.5 298.1C277.5 298.1 276.4 298 275.3 297.9L166.8 287.6L143.1 349.3L107.5 282Z" 
        stroke-linecap="round" stroke-linejoin="round" stroke-dasharray="10 10"
        stroke-dashoffset="14px"
        stroke-width="2"
      />
      <path
        vector-effect="non-scaling-stroke"
        class="speech-inner"
        d="M37.6 8.8C22.3 6.3 8.5 18.1 8.5 33.5V241.8C8.5 254.7 18.3 265.5 31.1 266.7L112.5 274.4L142 330L161.6 279L276.1 290C290.8 291.4 303.5 279.9 303.5 265.1V73.8C303.5 61.6 294.6 51.1 282.6 49.1L37.6 8.8Z" 
        fill="white" 
        stroke-width="1"
      />
      <g class="speech-exclaim">
        <text x="50" y="210" class="hi">
          Hi
        </text>
        <text x="205" y="210" class="bang">
          !
        </text>
      </g>
      
      <text></text>
    </svg>
  </div>

  <h1>
    My name is Zach. I’m an integrative 
      <span class="designer-contain">
        <span class="word designer" on:mouseenter={() => {desTL.play()}}>
          designer,
        </span>
        <div class="guide-wrap" style="right: 70%;bottom: .3em;">
          <div class="guide-y-ends">
            <div class="guide-y-line"></div>
          </div>
        </div>
        <div class="guide-wrap" style="right: .1em;bottom: 80%;">
          <div class="guide-x-ends">
            <div class="guide-x-line"></div>
        </div>
      </span> 
      <span class="word animator" on:mouseenter={() => {animTL.play()}}>
        animator
      </span> and
      <span class="word code" on:mouseenter={() => {codeTL.play()}}>
        <span class="code-tag code-open"></span>
        <span class="code-em">front-end dev.</span>
        <span class="code-tag code-close"></span>
      </span>
  </h1>
</section>


<style>
  section {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  h1 {
    margin-left: auto;
		margin-right: auto;
		max-width: 1200px;
  }
  
  /* SPEECH STYLES */
  .speech {
    /* safari can't handle the clamps!!! */
    width: 30vw;
    width: clamp(250px, 30vw, 400px);
  }
  .speech-exclaim {
    color: var(--gray);
    font-size: 9rem;
    /* skew the word */
    transform: translate(0px, -50px) skew(0deg, 6deg) scaleY(1.2);
    width: 100%;
    fill: var(--gray);
    font-family: Shackleton, serif;
  }
  .speech-inner {
    stroke: var(--gray);
  }
  :global(.crawling-line) {
    stroke: var(--acct);
    opacity: 1;
    animation: stroke 0.2s linear infinite;
  }

  @keyframes -global-stroke {
    to { 
      stroke-dashoffset: 0;
    }
  }
  
  /* TEXT ANIM STYLES */
  h1 span {
    position: relative;
    display: inline;
  }

  .word {
    cursor: pointer;
    display: inline-block;
  }
  
  .guide-wrap {
    position: absolute;
    --guides: 2px solid var(--acct);
  }

  .guide-x-ends {
    position: relative;
    border-left: var(--guides);
    border-right: var(--guides);
    height: 10px;
    opacity: 0;
  }
  
  .guide-y-ends {
    position: relative;
    border-top: var(--guides);
    border-bottom: var(--guides);
    width: 10px;
    right: 20px;
    opacity: 0;
  }

  .guide-x-line {
    border-top: var(--guides);
    width: 10px;
    position: relative;
    top: 4px;
  }
  .guide-y-line {
    border-left: var(--guides);
    height: 10px;
    position: relative;
    left: 4px;
  }

  /* .guide-text {
    color: white;
    font-size: 5px;
  } */

  .code {
    position: relative;
  }

  .code-em {
    display: inline-block;
  }

  .code-tag {
    font-family:'Courier New', Courier, monospace;
    color: white;
    background-color: var(--acct);
    font-weight: 500;
    font-size: 45%;
    line-height: 1.5;
    vertical-align: 40%;
    border-radius: 15px;
    margin: 0;
    position: absolute;
    top: 10%;
    visibility: hidden;
  }

  .code-open {
    left: -23%;
  }

  .code-close {
    right: -25%;
  }

  

</style>